.socials-list {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  justify-content: start;
  align-items: center;
  width: 156px;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
}

//   
//  
.socials-list__item-link {
  display: block;
  width: 48px;
  height: 48px;
  background-color: #81b3d2;

  //  
  &--vkontakte::before {
    content: "";
    position: absolute;
    width: 48px;
    height: 48px;
    background-image: url("../img/stack.svg#vkontakte-icon");
    background-position: center;
    background-repeat: no-repeat;
  }

  // 
  &--telegram::before {
    content: "";
    position: absolute;
    width: 48px;
    height: 48px;
    background-image: url("../img/stack.svg#telegram-icon");
    background-position: center;
    background-repeat: no-repeat;
  }

  //  
  &--youtube::before {
    content: "";
    position: absolute;
    width: 48px;
    height: 48px;
    background-image: url("../img/stack.svg#youtube-icon");
    background-position: center;
    background-repeat: no-repeat;
  }

  //      
  //    
  &:hover {
    background-color: #669ec0;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }

  //     
  &:active {
    background-color: #5496bd;
  }

  &--vkontakte:active::before,
  &--telegram:active::before,
  &--youtube:active::before {
    opacity: 0.3;
  }
}


//       
.socials-list__item-image:hover .socials-list__item-image-background {
  fill: #669ec0;
}

//       
.socials-list__item-image:active {
  .socials-list__item-image-background {
    fill: #5496bd;
  }

  .socials-list__item-image-icon {
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .socials-list {
    margin: 0;
  }
}
